import React,{ Component }  from 'react';
import { Button, Card ,Result,Popover,Divider} from 'antd';
import { Link} from 'react-router-dom';
import '../../App.css';
import airdrop from '../../ABI/LGairdrop';
import web3 from "../../ABI/web3";
import LG from '../../ABI/LG-ERC20';
import airdropEveryDay from '../../ABI/airdropEveryDay';
import { SmileOutlined, FrownTwoTone, MehTwoTone, LikeTwoTone, QuestionCircleOutlined} from '@ant-design/icons';

class lgairdrop extends Component {
    state = {
        AirdropAmount:"0",
        airdropadr:"0x03Cae53Cfadf3b3F109d16C96C4E44C440b4EeD0",  //LG空投合约地址
        airdropEveryDay_adr:"0x424F871bcdC17cFba4E5573E341e64718D16f221",  //每日空投合约地址
        AirdropAmountEveryDay:"0",
        time:"",
        message:"",
        disabled:'',
        disabled_b:'',
        user:'',
        _adayamount:'',

        LGcloseL:'none',
        LGcloseLM:'none',
        LGcloseGM:'none',
        LGcloseG:'none',
        lastHB:'',
      };
    
      async componentDidMount(){
        //判断页面是否安装了Metamask
        if(typeof window.ethereum !== 'undefined'){
          const ethereum = window.ethereum
          //禁止页面自动刷新，metamask要求写的
          ethereum.autoRefreshOnNetwoorkChange = false
          try{
            //第一次链接Metamask
            const accounts = await ethereum.enable()
        
            //初始化Provider
            const provider = window['ethereum']
            
            if(provider.chainId == 0x1){this.setState({chainId:"以太链"});
              this.setState({disabled:"disabled"})};
      
            //获取用户帐号
            window.defaultAccount = accounts[0].toLowerCase()
            this.setState({user:window.defaultAccount});

            //切换Metamask帐号
            ethereum.on('accountsChanged', function (accounts){
              console.log("accountsChanged:" + accounts)
            })
      
            //切换Metamaskm网络ID
            ethereum.on(function (networkVersion){
              console.log(networkVersion)
            })
      
          }catch(e){}
        }else{console.log('没有metamask')}
      
      
        //获取空投池LG余额
        const Airdrop_LGbalance = await LG.methods.balanceOf(this.state.airdropadr).call();
        this.setState({Airdrop_LGbalance});
        
       //获取每日一领池LG余额
        const AirdropEveryDay_LGbalance = await LG.methods.balanceOf(this.state.airdropEveryDay_adr).call();
        const cc = parseFloat(web3.utils.fromWei(AirdropEveryDay_LGbalance,"ether")).toFixed(0);
        this.setState({AirdropEveryDay_LGbalance:cc});

        //获取首页LG空投单个量
        const AirdropAmount = await airdrop.methods.AirdropAmount().call();
        if(AirdropAmount/10**18 <= Airdrop_LGbalance/10**18){
        this.setState({AirdropAmount:parseFloat(web3.utils.fromWei(AirdropAmount,"ether")).toFixed(1)})}

        //获取当日额
        const _adayamount = await airdropEveryDay.methods._adayamount().call();
        this.setState({_adayamount: parseFloat(web3.utils.fromWei(_adayamount,"ether")).toFixed(0)});
      

        //获取每日一领数量
        const AirdropAmountEveryDay = await airdropEveryDay.methods.AirdropAmount().call();
        this.setState({AirdropAmountEveryDay});
      
       if(_adayamount/10**18 < AirdropAmountEveryDay/10**18*2*0.9 ){
          this.setState({disabled_b:'disabled'})
        }
      
        //获取领取频率
        const time = await airdropEveryDay.methods.time().call();
        this.setState({time});

      
        }
    
        addDayamount =async () => {
          this.setState({message:"等待打包……"});
          await airdropEveryDay.methods.addDayamount().send({from:window.defaultAccount});
          this.setState({message:"供应成功，可继续领取"});
        }

        getAirdrop =async () => {
          this.setState({message:"等待打包……"});
          await airdrop.methods.getAirdrop().send({from:window.defaultAccount});
          this.setState({message:"领取成功请回钱包首页查看"});
        }

        getAirdropEveryDay = async () =>{
          this.setState({message:"等待打包……"});
          await airdropEveryDay.methods.getAirdrop().send({from:window.defaultAccount});
          this.setState({message:"领取成功请点击'查询结果'"});
        }
        // parseFloat(web3.utils.fromWei(lastHB,"ether")).toFixed(2);
        open = async () => {
          const lastHB = await airdropEveryDay.methods.lastHB(window.defaultAccount).call();
          const _lastHB = parseFloat(web3.utils.fromWei(lastHB,"ether")).toFixed(2);
          const bb = parseFloat(web3.utils.fromWei(this.state.AirdropAmountEveryDay,"ether")).toFixed(2);
          this.setState({lastHB:_lastHB});
        
          if( _lastHB <= (bb*2*0.11).toFixed(2)){
            this.setState({LGcloseL:"block"})
          }

          if( _lastHB > (bb*2*0.1).toFixed(2)  && _lastHB < (bb*2*0.5).toFixed(2)){
            this.setState({LGcloseLM:"block"})
          }

          if( _lastHB >= (bb*2*0.5).toFixed(2) && _lastHB < (bb*2*0.9).toFixed(2)){
            this.setState({LGcloseGM:"block"})
          }

          
          if( _lastHB == (bb*2*0.9).toFixed(2)){
            this.setState({LGcloseG:"block"})
          }
         
        }
        //关闭每日空投领取结果
        close = () => {
          this.setState({LGcloseL:"none"});
          this.setState({LGcloseLM:"none"});
          this.setState({LGcloseGM:"none"});
          this.setState({LGcloseG:"none"});
        }
    
  render(){

    const content = (
      <div>
        <p>为了防止撸客</p>
        <p>每日领取改为流量挖矿，领取的余额请在流量挖矿中查看，当满10枚时下一次的领取将自动转入您的帐户中</p>
        <p>添加了黑名单制度</p>
        <p>当每日额度用完时，任何人可以点击供应(间隔24小时)</p>
        <p>删除了原来的条件。更方便分享，您的朋友只需几分钱HT即可享受每日领取的乐趣</p>
      </div>
    );

  return (
        
        <Card  type="inner" title="LG通证空投(LGC&ge;10)" extra={<a href="https://hecoinfo.com/token/0x7cc0c1a34b1ae55f9bd2a19ece9d4b166b66e70c#balances">More</a>} >
            <p>新人福利：{this.state.AirdropAmount}
                <span style={{float:'right'}}>
                    <Button  onClick={() => {this.getAirdrop()}} 
                        disabled={this.state.disabled} 
                        type="primary"  
                        size='small'>
                        领取
                    </Button>
                </span>
            </p>
            <Divider >{this.state.time}天1领&nbsp;
                <Popover content={content} title="说明" trigger="click">
                    <QuestionCircleOutlined />
                </Popover>
            </Divider>
            <div>请在16号晚之前务必领取一次，使留存到帐。届时将添加信用系统，未到帐的留存将不补偿</div>
            <div>
                <span>总额:{this.state.AirdropEveryDay_LGbalance}</span>&nbsp;
                <span>日额:{this.state._adayamount}</span>&nbsp;
                
                max{(web3.utils.fromWei(this.state.AirdropAmountEveryDay,"ether")*2*0.9).toFixed(2)}&nbsp;
                
                
                <Button  style={{float:'right'}}
                        onClick={() => {this.addDayamount()}}
                        disabled={this.state.disabled} 
                        type="primary"  
                        size='small'>
                        供应
                    </Button>
                
            </div>

            <div style={{marginTop:'10px'}}>
                <Button onClick={() => {this.getAirdropEveryDay()}}
                    disabled={this.state.disabled_b}
                    type="primary" 
                    shape='round' 
                    size='small'
                    block>
                    领取
                </Button>
            </div>   
            
            <div style={{color:"green",fontSize:"15px"}}>
                  {this.state.message}
          </div>

          <Button shape='round' type="primary" block color='green' size="middle" style={{marginTop:'10px'}} onClick={() => {this.open()}}>查询结果</Button>
    
      <span>   
          <Result style={{display:this.state.LGcloseL}}
              icon={< FrownTwoTone />}
              title={this.state.lastHB}
              extra={<Button shape='round' type="primary" onClick={() => {this.close()}}>关闭</Button>}
            />

            <Result style={{display:this.state.LGcloseLM}}
              icon={< MehTwoTone />}
              title={this.state.lastHB}
              extra={<Button shape='round' type="primary" onClick={() => {this.close()}}>关闭</Button>}
            />

            <Result style={{display:this.state.LGcloseGM}}
              icon={< SmileOutlined />}
              title={this.state.lastHB}
              extra={<Button shape='round' type="primary" onClick={() => {this.close()}}>关闭</Button>}
            />

            <Result style={{display:this.state.LGcloseG}}
              icon={< LikeTwoTone />}
              title={this.state.lastHB}
              extra={<Button shape='round' type="primary" onClick={() => {this.close()}}>关闭</Button>}
            />
      </span>
        </Card>
                
      
  );
}
}

export default lgairdrop;
